<template>
  <a-form-model
    ref="form"
    :model="entity"
    layout="vertical"
    autocomplete="off"
    :rules="rules"
    class="h-father"
  >
    <g-body-form-layout>
      <!-- 搜索插槽 -->
      <template #search-card>
        <div class="search-card">
          <g-container-list :value.sync="entity.containerName">
          </g-container-list>
        </div>
      </template>
      <!-- 展开插槽 -->
      <template #expand-card>
        <div class="expand-card">
          <g-collapse headerName="批次信息">
            <template #content>
              <a-row :gutter="24">
                <a-col :span="6">
                  <a-form-model-item label="批次编号">
                    <a-input v-model="entity.batchNo" disabled />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="物料编码">
                    <a-input v-model="entity.materialCode" disabled />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="物料名称">
                    <a-input v-model="entity.materialName" disabled />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="当前状态">
                    <a-input v-model="entity.currentStatus" disabled />
                  </a-form-model-item>
                </a-col>
              </a-row>
              <a-row :gutter="24">
                <a-col :span="6">
                  <a-form-model-item label="供应商" prop="supplier">
                    <a-input v-model="entity.supplier" />
                  </a-form-model-item>
                </a-col>
                <a-col :span="6">
                  <a-form-model-item label="生产日期" prop="productionDate">
                    <a-date-picker v-model="entity.productionDate" style="width: 100%" />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </template>
          </g-collapse>
        </div>
      </template>
      <template #card-body>
        <g-collapse headerName="数据采集">
          <template #content>
            <g-data-points
              :dataPoints="entity.dataPoints"
              validDataName="dataPoints"
            >
            </g-data-points>
          </template>
        </g-collapse>

        <g-collapse headerName="修改原因">
          <template #content>
            <a-row :gutter="24">
              <a-col :span="12">
                <a-form-model-item label="修改原因" prop="changeReason" :rules="[{ required: true, message: '请输入修改原因' }]">
                  <a-textarea
                    rows="4"
                    v-model="entity.changeReason"
                    placeholder="请输入修改原因"
                    :max-length="1000"
                  ></a-textarea>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </template>
      <template #g-fixed-row>
        <div class="g-fixed-row">
          <g-btn size="large" text="刷新" @callback="refresh()"></g-btn>
          <g-btn size="large" text="提交" type="primary" @callback="submitChange()">
          </g-btn>
        </div>
      </template>
    </g-body-form-layout>
  </a-form-model>
</template>

<script>
import GSignModal from '@/components/g-sign-modal/g-sign-modal.vue';
import { createModal } from '@/utils';
import moment from 'moment';

export default {
  name: 'DataV2InfoUpdate',

  data() {
    return {
      entity: {
        containerName: "",
        batchNo: "",
        materialCode: "",
        materialName: "",
        currentStatus: "",
        supplier: "", // 新增供应商
        productionDate: null, // 新增生产日期
        changeReason: "",
        dataPoints: [
          {
            dataValue: "",
            dataPointName: "操作人员",
            isRequired: true,
            dataType: "String",
          },
          {
            dataValue: "",
            dataPointName: "操作时间",
            isRequired: true,
            dataType: "Timestamp",
          }
        ],
      },
      rules: {
        containerName: [
          { required: true, message: '请选择批次', trigger: 'change' }
        ],
        supplier: [
          { required: true, message: '请输入供应商', trigger: 'blur' }
        ],
        productionDate: [
          { required: true, message: '请选择生产日期', trigger: 'change' }
        ],
        changeReason: [
          { required: true, message: '请输入修改原因', trigger: 'blur' }
        ]
      }
    };
  },

  watch: {
    "entity.containerName": {
      handler() {
        this.$refs.form && this.$refs.form.validateField("containerName");
        if (this.entity.containerName) {
          this.fetchBatchInfo();
        }
      },
    },
  },

  mounted() {
    // 初始化页面数据
  },

  methods: {
    // 获取批次信息
    fetchBatchInfo() {
      // 模拟获取批次信息
      // 实际项目中应该调用API获取批次信息
      setTimeout(() => {
        this.entity.batchNo = "B" + Math.floor(Math.random() * 10000);
        this.entity.materialCode = "M" + Math.floor(Math.random() * 10000);
        this.entity.materialName = "测试物料";
        this.entity.currentStatus = "在库";
        this.entity.supplier = "供应商" + Math.floor(Math.random() * 10);
        this.entity.productionDate = moment().subtract(Math.floor(Math.random() * 30), 'days');
      }, 500);
    },

    // 提交信息变更
    submitChange() {
      this.$refs.form.validate().then((valid) => {
        if (!valid) {
          return;
        }
        this.createSignModal();
      });
    },

    // 创建签名弹窗
    createSignModal() {
      createModal({
        title: '签名确认',
        width: '600px',
      }, {
        component: GSignModal,
        success: (v) => {
          console.log('签名信息:', v);
          this.saveInfoUpdate();
        },
      },);
    },

    // 保存信息变更
    saveInfoUpdate() {
      // 模拟API调用, 实际项目中应发送请求到后端
      const data = {
        ...this.entity,
        productionDate: this.entity.productionDate ? this.entity.productionDate.format('YYYY-MM-DD') : null,
      };
      console.log("提交的数据:", data); // 实际项目中应移除此行
      this.$message.success('批次信息更新成功');
      this.refresh();
    },

    // 刷新页面
    refresh() {
      this.entity = {
        containerName: "",
        batchNo: "",
        materialCode: "",
        materialName: "",
        currentStatus: "",
        supplier: "",
        productionDate: null,
        changeReason: "",
        dataPoints: [
          {
            dataValue: "",
            dataPointName: "操作人员",
            isRequired: true,
            dataType: "String",
          },
          {
            dataValue: "",
            dataPointName: "操作时间",
            isRequired: true,
            dataType: "Timestamp",
          }
        ],
      };
    }
  },
};
</script>

<style lang="scss" scoped>
.search-card {
  margin-bottom: 16px;
}

.g-fixed-row {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
}
</style>